                    </div>
                    <div class="aui-car-box-list-text-arithmetic">
                      <a
                        href="javascript:;"
                        class="minus"
                        @click="CHANGE_ITEM({_id:item._id,cart_number:-1})"
                      >-</a>
                      <span class="num">{{item.cart_number}}</span>
                      <a
                        href="javascript:;"
                        class="plus"
                        @click="CHANGE_ITEM({_id:item._id,cart_number:1})"
                      >+</a>
                    </div>
                  </div>
				  <button id="btn" @click="REMOVE_ITEM({_id:item._id})">删除</button>
                </div>
              </div>
            </li>
